  margin: 0; }

@media screen and (min-width: 32em) {
  .sitenav {
    float: right;
    margin-left: 0;
    margin-right: .5rem; } }

.sitebottom {
  padding: 4rem 0; }
  .sitebottom a {
    color: inherit; }
    .sitebottom a:hover, .sitebottom a:focus {
      color: #660099; }
      .ccol-1 .sitebottom a:hover, .ccol-1 .sitebottom a:focus {
        color: #336699; }
      .ccol-2 .sitebottom a:hover, .ccol-2 .sitebottom a:focus {
        color: #669933; }
      .ccol-3 .sitebottom a:hover, .ccol-3 .sitebottom a:focus {
