  text-transform: uppercase;
  font-size: .875rem; }
  .menu > li > a:hover, .menu > li > a:focus {
    color: #0a0a0a; }
  .menu > li > a.active {
    color: #660099; }
    .ccol-1 .menu > li > a.active {
      color: #336699; }
    .ccol-2 .menu > li > a.active {
      color: #669933; }
    .ccol-3 .menu > li > a.active {
      color: #efa331; }
    .ccol-4 .menu > li > a.active {
      color: #660099; }

.subnav {
  background-color: #660099; }
  .ccol-1 .subnav {
    background-color: #336699; }
  .ccol-2 .subnav {
    background-color: #669933; }
  .ccol-3 .subnav {
    background-color: #efa331; }
  .ccol-4 .subnav {
    background-color: #660099; }

.tabs--submenu {
  margin-left: -1.5625rem;
  margin-right: -1.5625rem; }
  @media screen and (min-width: 64em) {
    .tabs--submenu {
      margin-left: 0;
      margin-right: 0; } }

.tabs {
  border: none; }

.tabs-title > a {
  font-weight: bold;
  color: rgba(254, 254, 254, 0.95); }
  @media screen and (min-width: 64em) {
    .tabs-title > a {
      font-size: .875rem; } }

.tabs-title > a:not([aria-selected="true"]):hover {
  background-color: rgba(254, 254, 254, 0.17);
  color: #fefefe; }

.tabs-title > a[aria-selected="true"] {
  color: #660099; }
  .ccol-1 .tabs-title > a[aria-selected="true"] {
    color: #336699; }
  .ccol-2 .tabs-title > a[aria-selected="true"] {
    color: #669933; }
  .ccol-3 .tabs-title > a[aria-selected="true"] {
    color: #efa331; }
  .ccol-4 .tabs-title > a[aria-selected="true"] {
